export default {
	data() {
		return {
			news: []
		}
	},
	onLoad: function() {
		uni.showLoading({
			title: '加载中....',
			mask: false
		});
		uni.request({ //ureq
			url: 'https://unidemo.dcloud.net.cn/api/news',
			method: 'GET',
			data: {},
			success: res => {
				console.log(res);
				this.news = res.data;
				uni.hideLoading();
			},
			fail: () => {},
			complete: () => {}
		});
	},
	methods: {
		openinfo(e) {
			var newsid = e.currentTarget.dataset.newsid; //获取点击的id  通过data-newsid 绑定
			console.log(newsid);
			uni.navigateTo({ //unav
				url: '../info/info?newsid='+ newsid,
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		}
	}
}
